@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
html, body, #root {
    height: 100%;
    min-height: 100%;
    width: 100%;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.4rem;
    background: linear-gradient(to top right, #115DA3, #4ECFED);
    color: #4A4A4A;
}

#root {
    display: flex;
}

.container {
    max-width: 768px;
}

h1 {
    font-size: 4.8rem;
    font-weight: 400;
}

h2 {
    font-size: 2.4rem;
    font-weight: 400;
}

h3 {
    font-size: 1.8rem;
    font-weight: 600;
}

h4 {
    font-size: 1.4rem;
    font-weight: 600;
}

.wi {
    color: #65AED5;
    font-size: 40px;
}

@media only screen and (min-width: 576px) {
    #root {
        align-items: center;
        justify-content: center;
        overflow: hidden;
    }
}
.title {
    margin-bottom: 12px;
    margin-top: 12px;
    display: flex;
    justify-content: space-between;
}

.title h3, .title p {
    color: white
}

.title h3 {
    margin-bottom: 0;
}

.title p {
    margin-bottom: 0;
}

@media only screen and (min-width: 576px) {
    .title {
        margin-bottom: 20px;
        display: block;
        justify-content: baseline;
    }
}
.switch {
    position: relative;
    display: inline-block;
    width: 55px;
    height: 26px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #2685BD;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: .4s;
}

.units:before {
    position: absolute;
    content: "C\00B0";
    font-weight: 600;
    color: #2685BD;
    left: 5px;
    bottom: 3px;
    transition: .4s;
}

.units:after {
    position: absolute;
    content: "F\00B0";
    font-weight: 600;
    color: #2685BD;
    right: 4px;
    bottom: 3px;
    transition: .4s;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2685BD;
}

input:checked + .slider:before {
    -webkit-transform: translateX(29px);
    transform: translateX(29px);
}

.units.fahrenheit:before {
    color: white;
}

.units.celsius:after {
    color: white;
}

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
.header {
    background: #D9FBFF;
    margin-right: -7.5px;
    margin-left: -7.5px;
    padding: 16px 16px 0 16px;
}

.header h1, 
.header h4 {
    color: #65AED5;
}

.header .animated-container:after {
    content: "\00B0";
}

@media only screen and (min-width: 576px) {
    .header {
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
    }
}


.skyline {
    height: 175px;
    background-size: cover;
    background-position: bottom center;
    position: relative;
}

.cloud {
    display: none;
}

@-webkit-keyframes floating {
    0% {
        top: 50px;
    }

    50% {
        top: 45px;
    }

    100% {
        top: 50px;
    }
}

@keyframes floating {
    0% {
        top: 50px;
    }

    50% {
        top: 45px;
    }

    100% {
        top: 50px;
    }
}


@media only screen and (min-width: 400px) {
    .skyline {
        height: 200px;
    }
}

@media only screen and (min-width: 450px) {
    .skyline {
        height: 250px;
    }
}

@media only screen and (min-width: 576px) {
    .skyline {
        height: 330px;
    }
}

@media only screen and (min-width: 768px) {
    .cloud {
        position: absolute;
        top: 50px;
        max-width: 25%;
        display: block;
    }
    .left-cloud {
        -webkit-animation: floating 2s infinite ease-in-out;
                animation: floating 2s infinite ease-in-out;
        left: -50px;
    }
    .right-cloud {
        -webkit-animation: floating 2s infinite 1s ease-in-out;
                animation: floating 2s infinite 1s ease-in-out;
        right: -50px;
    }
}

@media only screen and (min-width: 992px) {
    .left-cloud {
        left: -100px;
    }
    
    .right-cloud {
        right: -75px;
    }
}


.day {
    padding: 16px;
}

.day .wi {
    font-size: 30px;
}

.day:not(:last-child) {
    border-bottom: 1px solid #D8D8D8;
}

.day .animated-container {
    justify-content: center;
}

.day .animated-container:after {
    content: "\00B0";
}

@media only screen and (min-width: 576px) {
    .day:not(:last-child) {
        border-right: 1px solid #D8D8D8;
    }
    .day .wi {
        font-size: 40px;
    }
}
.forecast {
    background: white;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

@media only screen and (min-width: 576px) {
    .forecast {
        border-bottom-left-radius: 3px;
        border-bottom-right-radius: 3px;
    }
}
@media only screen and (min-width: 576px) {
    .frame {
        box-shadow: 0 0 15px rgba(0,0,0, .2);
    }
}
